@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

:where(ul, ol):where([class]) {
  padding-left: 0;
}

body,
:where(blockquote, figure):where([class]) {
  margin: 0;
}

:where(h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
dl):where([class]) {
  margin-block: 0;
}

:where(dd[class]) {
  margin-left: 0;
}

:where(fieldset[class]) {
  margin-left: 0;
  padding: 0;
  border: none;
}

:where(ul[class]) {
  list-style: none;
}

p {
  --paragraphMarginBottom: 24px;
  margin-block: 0;
}

p:where(:not([class]):not(:last-child)) {
  margin-bottom: var(--paragraphMarginBottom);
}

img {
  display: block;
  max-width: 100%;
}

input,
textarea,
select,
button {
  font: inherit;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  line-height: 1.5;
}

svg *[fill] {
  fill: currentColor;
}

svg *[stroke] {
  stroke: currentColor;
}

svg * {
  transition-property: fill, stroke;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
@font-face {
  font-family: "Inter Tight";
  src: url("../fonts/InterTight-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter Tight";
  src: url("../fonts/InterTight-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Studio Pro";
  src: url("../fonts/studio-pro-regular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Studio Pro";
  src: url("../fonts/studio-pro-bold.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --color-dark: #141414;
  --color-dark-mute: rgba(20, 20, 20, 0.6);
  --color-light: #fbfbfb;
  --color-light-mute: rgba(251, 251, 251, 0.4);
  --color-green: #576f35;
  --color-green-mute: rgba(7, 111, 53, 0.3);
  --color-purple: #7a3145;
  --border: 1px solid rgba(251, 251, 251, 0.2);
  --border-radius: 30px;
  --border-radius-small: 15px;
  --border-radius-large: 40px;
  --font-family-base: "Inter Tight", sans-serif;
  --font-family-alt: "Studio Pro", sans-serif;
  --container-width: 1080px;
  --container-padding-x: 100px;
  --grid-content-padding: 16%;
  --section-padding-y: 100px;
  --input-heigth: 52px;
  --button-heigth: 52px;
  --transition-duration: 0.2s;
}
@media (max-width: 768.98px) {
  :root {
    --container-width: 704px;
    --container-padding-x: 32px;
    --grid-content-padding: 13% ;
  }
}
@media (max-width: 500.98px) {
  :root {
    --section-padding-y: 80px;
    --container-padding-x: 20px;
  }
}

.container {
  max-width: calc(var(--container-width) + var(--container-padding-x) * 2);
  margin-inline: auto;
  padding-inline: var(--container-padding-x);
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

@media (max-width: 768.98px) {
  .hidden-tablet {
    display: none !important;
  }
}

@media (min-width: 769px) {
  .visible-tablet {
    display: none !important;
  }
}

body {
  font-size: clamp(14px, 16 / 768 * 100vw, 16px);
  position: relative;
  font-family: var(--font-family-base);
  font-weight: 500;
  line-height: 1.25;
  color: var(--color-light);
  background-color: var(--color-purple);
}
body.stop-scroll {
  overflow: hidden;
}

a,
button,
input,
textarea,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2 {
  font-weight: 600;
}

h1 {
  font-size: clamp(48px, 76 / 768 * 100vw, 76px);
}

h2 {
  font-size: clamp(48px, 64 / 768 * 100vw, 64px);
}

.grid {
  --gridColums: 1;
  display: grid;
  grid-template-columns: repeat(var(--gridColumns), 1fr);
  column-gap: 21px;
  row-gap: 28px;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.grid_exclusive-grid {
  grid-template-columns: minmax(0, 630px) minmax(0, 650px);
  gap: 0;
}
@media (max-width: 768.98px) {
  .grid_exclusive-grid {
    grid-template-columns: 1fr;
  }
}
.grid--2 {
  --gridColumns: 2;
}
.grid--3 {
  --gridColumns: 3;
}
.grid--4 {
  --gridColumns: 4;
}
.grid--5 {
  --gridColumns: 5;
}
.grid--6 {
  --gridColumns: 6;
}
.grid--7 {
  --gridColumns: 7;
}
.grid--8 {
  --gridColumns: 8;
}
.grid--9 {
  --gridColumns: 9;
}
.grid--10 {
  --gridColumns: 10;
}
.grid--11 {
  --gridColumns: 11;
}
.grid--12 {
  --gridColumns: 12;
}
@media (max-width: 768.98px) {
  .grid--3 {
    --gridColumns: 2;
  }
}
@media (max-width: 500.98px) {
  .grid--3 {
    --gridColumns: 1;
  }
}
@media (max-width: 768.98px) {
  .grid--2 {
    --gridColumns: 1;
  }
}

.section {
  padding-block: var(--section-padding-y);
}
.section_while-bg {
  background-color: var(--color-light);
  color: var(--color-dark);
}
.section_purple-bg {
  background-color: var(--color-purple);
  color: var(--color-light);
}
.section__header {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  margin-bottom: 50px;
}
@media (max-width: 500.98px) {
  .section__header {
    row-gap: 20px;
    margin-bottom: 40px;
  }
}
.section__title {
  font-size: clamp(48px, 64 / 768 * 100vw, 64px);
  font-family: var(--font-family-base);
  font-weight: 600;
  line-height: 1;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: currentColor;
}
@media (max-width: 500.98px) {
  .section__title {
    line-height: 1.04;
  }
}
.section__description {
  color: currentColor;
}
@media (max-width: 500.98px) {
  .section__description {
    line-height: 1.29;
  }
}
.puddle-bg {
  padding-inline: 13px;
  background-color: var(--color-green);
  border-radius: 12px;
  color: var(--color-light);
}

.text-accent {
  color: rgba(251, 251, 251, 0.3);
}
.text-accent_green {
  color: var(--color-green);
}
.text-accent_purple {
  color: var(--color-purple);
}

.overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(20, 20, 20, 0.4);
  z-index: 1;
  display: none;
}
@media (max-width: 768.98px) {
  .overlay.overlay_active {
    display: block;
  }
}

.button {
  font-size: clamp(12px, 18 / 768 * 100vw, 18px);
  border-radius: var(--border-radius);
  border: none;
  height: var(--button-heigth);
  background-color: var(--color-purple);
  font-family: var(--font-family-base);
  font-weight: 600;
  line-height: 1.11;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: var(--color-light);
  cursor: pointer;
}

.burger-menu {
  display: block;
  height: 40px;
  width: 40px;
  position: absolute;
  bottom: 15px;
  right: var(--container-padding-x);
  padding: 8px 5px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.burger-menu .line {
  display: block;
  height: 4px;
  width: 100%;
  border-radius: 10px;
  background-color: currentColor;
}
.burger-menu .line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}
.burger-menu .line2 {
  width: 50%;
  transition: transform 0.2s ease-in-out;
}
.burger-menu .line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}
.burger-menu_active .line1 {
  transform: rotate(45deg);
}
.burger-menu_active .line2 {
  transform: scaleY(0);
}
.burger-menu_active .line3 {
  transform: rotate(-45deg);
}

.list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  list-style-type: none;
}
.list__item {
  font-size: clamp(14px, 16 / 768 * 100vw, 16px);
  position: relative;
  border-radius: 15px;
  height: 76px;
  padding: 12px 27px 12px 100px;
  background-color: var(--color-light);
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: var(--font-family-alt);
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: -0.02em;
  color: var(--color-dark-mute);
}
@media (max-width: 500.98px) {
  .list__item {
    text-wrap: balance;
    font-size: 12px;
  }
}
.list__item span {
  position: absolute;
  left: 29px;
  top: 50%;
  transform: translateY(-50%);
  width: 42px;
  height: 42px;
  background-color: #7a3145;
  border-radius: 50%;
  font-family: var(--font-family-base);
  font-weight: 600;
  font-size: 20px;
  line-height: 1;
  letter-spacing: -0.02em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  box-shadow: 0 0 0 5px rgba(122, 49, 69, 0.3);
}
.list__item strong {
  display: contents;
  color: var(--color-dark);
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  margin-inline: auto;
  width: 100%;
  padding-top: 32px;
  transition: padding-top 0.3s ease-out;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
  height: 52px;
}
@media (max-width: 500.98px) {
  .header__logo {
    z-index: 3;
  }
}
.header__logo-image {
  max-width: 194px;
  width: 100%;
}
.header__navigation {
  --viewportWidth: 46vw; /* по макету на брейкпоинт 768 размер бокового меню = 351 / 768 */
  transition: transform 0.5s ease-in-out;
}
@media (max-width: 768.98px) {
  .header__navigation {
    position: absolute;
    z-index: 2;
    height: 100vh;
    width: var(--viewportWidth);
    margin: 0 auto;
    top: 0;
    right: 0;
    background-color: var(--color-purple);
    transform: translateX(var(--viewportWidth));
  }
}
@media (max-width: 500.98px) {
  .header__navigation {
    --viewportWidth: 100%;
    width: var(--viewportWidth);
    transform: translateX(var(--viewportWidth));
  }
}
.header__navigation_active {
  transform: translateX(0);
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
}
@media (max-width: 768.98px) {
  .header__nav {
    margin-top: auto;
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }
}
.header__button {
  padding: 12px 32px;
}
@media (max-width: 768.98px) {
  .header__button {
    margin-top: auto;
    margin-bottom: 50px;
    background-color: var(--color-light);
    color: var(--color-purple);
    font-size: 18px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  column-gap: 8px;
}
@media (max-width: 768.98px) {
  .nav__list {
    flex-direction: column;
    row-gap: 8px;
    margin-top: auto;
  }
}
.nav__item {
  border: 1px solid rgba(251, 251, 251, 0.4);
  border-radius: 12px;
  padding: 12px;
  color: var(--color-light);
  cursor: pointer;
}
.nav__item_active {
  background-color: var(--color-light);
  color: var(--color-purple);
}
.nav__item--focused {
  color: var(--color-dark);
}
.nav__link {
  font-family: var(--font-family-base);
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: -0.02em;
}
@media (max-width: 768.98px) {
  .nav__link {
    text-align: center;
    max-width: 123px;
    width: 100%;
  }
}
@media (hover: hover) {
  .nav__link:hover {
    background-color: var(--color-light);
    color: var(--color-purple);
  }
}
@media (hover: none) {
  .nav__link:active {
    background-color: var(--color-light);
    color: var(--color-purple);
  }
}
.nav__link:focus {
  outline: none;
}

.order-instructions {
  background: linear-gradient(90deg, var(--color-purple) 50%, var(--color-green) 50%);
}
@media (max-width: 768.98px) {
  .order-instructions {
    background: var(--color-green);
  }
}
.order-instructions__image-wrapper {
  padding: var(--grid-content-padding);
  height: 100%;
}
@media (max-width: 768.98px) {
  .order-instructions__image-wrapper {
    background-color: var(--color-purple);
    padding: var(--grid-content-padding) 64px;
  }
}
@media (max-width: 500.98px) {
  .order-instructions__image-wrapper {
    padding: 40px var(--container-padding-x);
  }
}
.order-instructions__image {
  filter: brightness(0.8);
  border-radius: var(--border-radius);
  object-fit: none;
  object-position: 50% 25%;
  width: 100%;
}
@media (max-width: 768.98px) {
  .order-instructions__image {
    max-width: 640px;
    aspect-ratio: 1;
    object-position: 50% 100%;
  }
}
@media (min-width: 501px) {
