    margin: auto;
    max-width: 450px;
    padding-inline: 0;
  }
}
@media (max-width: 500.98px) {
  .order-instructions__body {
    padding: 80px var(--container-padding-x);
  }
}
.order-instructions__title {
  font-size: clamp(28px, 32 / 768 * 100vw, 32px);
  font-family: var(--font-family-base);
  font-weight: 600;
  line-height: 1;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: var(--color-light);
  margin-bottom: 25px;
}
@media (max-width: 500.98px) {
  .order-instructions__title {
    margin-bottom: 20px;
  }
}
.order-instructions__description {
  font-size: clamp(14px, 16 / 768 * 100vw, 16px);
  font-family: var(--font-family-base);
  font-weight: 500;
  line-height: 1.25;
  color: var(--color-light);
  margin-bottom: 50px;
}
@media (max-width: 500.98px) {
