.contact-us__legend {
  font-family: var(--font-family-base);
  font-weight: 600;
  font-size: 24px;
  line-height: 108%;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: var(--color-dark);
  margin-bottom: 25px;
}
.contact-us__button {
  background-color: var(--color-green);
  color: var(--color-light);
}
@media (max-width: 768.98px) {
